@import "./common/root" , 
        "./layout/grid" , 
        "./common/common";

        body {
            background: #000;
        }

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 70px;
    transition: 0.5s;
    z-index: 7;

    &::before {
        content: '';
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transition: 0.5s;
        background-color: rgba(255,255,255,0.3);
    }

    &:hover::before {
        background-color: rgba(255,255,255,1);
    }


    &-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 100%;
        position: relative;
        z-index: 6;
    }

    &__list {
        display: flex;
        align-items: center;

        @include media($sm){
            position: fixed;
            top: 0;
            left: -100%;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,0.8);
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            z-index: 4;
            transition: 0.5s;

            &.active {
                left: 0;
            }
        }
    }

    &__logo {
        width: 100px;
        height: 80px;
        position: relative;
        z-index: 999;
        
        & img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__link {
        display: inline-block;
        margin: 0 15px;
        text-transform: uppercase;
        color: #000;

        @include media($sm) {
            color: #fff;
        }
    }


    &__burger {
        width: 40px;
        height: 30px;
        overflow: hidden;
        position: relative;
        display: none;
        
        &.active::before {
            top: 14px;
            transform: rotate(45deg);
        }
        &.active::after {
            top: 14px;
            transform: rotate(-45deg);
            width: 100%;
        }
        &.active span {
            transform: scaleX(0);
        }
        


        @include media($sm){
            display: block;
            z-index: 999;
            
        }

        &::before, 
        &::after, 
        & span {
            position: absolute;
            right: 0;
            display: block;
            height: 2px;
            background: blue;
            transition: 0.5s;
        }
        &::before,
        &::after {
            content: '';
        }
        &::before {
            top: 0;
            width: 100%;
        }
        &::after {
            bottom: 0;
            width: 40%;
        }
        & span {
            top: 14px;
            width: 70%;
            transition: 0.1s;
            
        }
    }
   
}


.start {
    width: 100%;
    height: 100vh;
    overflow: hidden;

    &-wrapper {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
    }

    &-slide {
        flex: 1 0 100%;
        width: 100%;
        height: 100%;
        background: red;

        & .container {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }

        &-wrapper {
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
        }
    }

    &-pagination {
        position: absolute;
        bottom: 10px;
        left: 0;
        width: 100%;
        display: flex;
        justify-content: center;
    }
}