* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-right: 15px;
  padding-left: 15px;
  height: 100%;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

body {
  background: #000;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  transition: 0.5s;
  z-index: 7;
}

.header::before {
  content: '';
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 0.5s;
  background-color: rgba(255, 255, 255, 0.3);
}

.header:hover::before {
  background-color: white;
}

.header-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 6;
}

.header__list {
  display: flex;
  align-items: center;
}

@media (max-width: 768px) {
  .header__list {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 4;
    transition: 0.5s;
  }
  .header__list.active {
    left: 0;
  }
}

.header__logo {
  width: 100px;
  height: 80px;
  position: relative;
  z-index: 999;
}

.header__logo img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header__link {
  display: inline-block;
  margin: 0 15px;
  text-transform: uppercase;
  color: #000;
}

@media (max-width: 768px) {
  .header__link {
    color: #fff;
  }
}

.header__burger {
  width: 40px;
  height: 30px;
  overflow: hidden;
  position: relative;
  display: none;
}

.header__burger.active::before {
  top: 14px;
  transform: rotate(45deg);
}

.header__burger.active::after {
  top: 14px;
  transform: rotate(-45deg);
  width: 100%;
}

.header__burger.active span {
  transform: scaleX(0);
}

@media (max-width: 768px) {
  .header__burger {
    display: block;
    z-index: 999;
  }
}

.header__burger::before, .header__burger::after,
.header__burger span {
  position: absolute;
  right: 0;
  display: block;
  height: 2px;
  background: blue;
  transition: 0.5s;
}

.header__burger::before, .header__burger::after {
  content: '';
}

.header__burger::before {
  top: 0;
  width: 100%;
}

.header__burger::after {
  bottom: 0;
  width: 40%;
}

.header__burger span {
  top: 14px;
  width: 70%;
  transition: 0.1s;
}

.start {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.start-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.start-slide {
  flex: 1 0 100%;
  width: 100%;
  height: 100%;
  background: red;
}

.start-slide .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.start-slide-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.start-pagination {
  position: absolute;
  bottom: 10px;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
}
